import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Works from '@/components/Works'
import News from '@/components/News'
import Wxs from '@/components/Wxs'
import About from '@/components/About'
import Login from '@/components/Login'
import Investor from '@/components/Investor'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/hello',
      name: 'Hello',
      component: Hello,
      children: [{
        path: '/works',
        name: 'Works',
        component: Works
      }, {
        path: '/news',
        name: 'News',
        component: News
      }, {
        path: '/wxs',
        name: 'Wxs',
        component: Wxs
      }, {
        path: '/about',
        name: 'About',
        component: About
      }, {
        path: '/investor',
        name: 'Investor',
        component: Investor
      }]
    }, {
        path: '/login',
        name: 'Login',
        component: Login
      }
  ]
})



